







import { Component, Vue } from 'vue-property-decorator';
import Success from './Success.vue';
import Failure from './Failure.vue';
import { getStorage } from '../store';

@Component({
  name: 'Home',
  components: {
    Success,
    Failure,
  },
})
export default class Home extends Vue {
  data = null;

  isWin = 0;

  async mounted() {
    const storage = getStorage();
    if (!storage || !storage.phone) {
      this.$router.replace({ name: 'second' });
      return false;
    }
    try {
      const data = await this.$xhr.api.get('/lottery2023/check_activity', {
        params: {
          phone: storage.phone,
        },
      });
      if (!data.id || data.awardStatus == -1) {
        this.$router.replace({ name: 'second' });
        return;
      }
      if (data.awardId == -2) {
        this.$router.replace({ name: 'failure', params: { data } });
        return;
      }
      if (data.awardId > 0 && data.awardStatus != 1) {
        this.$router.replace({ name: 'success', params: { data } });
        return;
      }
      if (data.awardId > 0 && data.awardStatus == 1) {
        this.$router.replace({ name: 'receive', params: { data } });
        return;
      }
    } catch (error) {
      this.$router.replace({ name: 'second' });
      this.$toast(error && error.msg);
    }
  }
}
